<template>
	<div>
		<nav class="navbar navbar-transparent navbar-absolute">
			<div class="container">
				<div class="navbar-header">
					<button type="button" class="navbar-toggle"
							data-toggle="collapse"
							data-target="#navigation-example-2"
							@click="toggleNavbar">
						<span class="sr-only">Переключить навигацию</span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
					</button>
					<span class="navbar-brand">Reagent Store</span>
				</div>
				<div class="collapse navbar-collapse">
					<ul class="nav navbar-nav navbar-right">
						<router-link to="/register" tag="li"><a>Регистрация</a></router-link>
					</ul>
				</div>
			</div>
		</nav>
		<div class="wrapper wrapper-full-page">
			<div class="full-page login-page" data-color="blue" data-image="static/img/background/background.jpg">
				<!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
				<div class="content">
					<div class="container">
						<div class="row">
							<div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3">
								<form method="#" action="#">
									<div class="card" data-background="color" data-color="blue">
										<div class="card-header"><h3 class="card-title">Войти</h3></div>
										<div class="card-content">
											<div class="form-group">
												<label>Эл.почта</label>
												<input type="email" name="email" v-validate="modelValidations.email" v-model="model.email"
													placeholder="Введите Эл.почту" class="form-control input-no-border" data-vv-as="Эл.почта">
												<small class="text-danger" v-show="email.invalid">{{ errors.first('email') }}</small>
											</div>
											<div class="form-group">
												<label>Пароль</label>
												<input type="password" name="password" v-validate="modelValidations.password" v-model="model.password"
													placeholder="Пароль" class="form-control input-no-border" data-vv-as="Пароль">
												<small class="text-danger" v-show="password.invalid">{{ errors.first('password') }}</small>
											</div>
										</div>
										<div class="card-footer text-center">
											<button type="submit" class="btn btn-fill btn-wd" @click.prevent="validate" v-bind:disabled="processLogin">Продолжить</button>
											<div class="forgot m-t-10"><router-link to="/forgot">Забыли пароль?</router-link></div>
										</div>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
				<footer class="footer footer-transparent">
					<div class="container"><div class="copyright">&copy; 2019, NSK</div></div>
				</footer>
				<div class="full-page-background" style="background-image: url(static/img/background/background.jpg) "></div>
			</div>
		</div>
		<div class="collapse navbar-collapse off-canvas-sidebar">
			<ul class="nav nav-mobile-menu">
				<router-link to="/register" tag="li"><a>Регистрация</a></router-link>
			</ul>
		</div>
	</div>
</template>
<script>
	import { mapFields } from 'vee-validate'
	import swal from 'sweetalert2'
	export default {
		computed: {
			...mapFields(['email', 'password'])
		},
		props: {
			referralCode: {
				default: null
			}
		},
		data () {
			return {
				model: {
					email: '',
					password: ''
				},
				modelValidations: {
					email: {
						required: true,
						email: true
					},
					password: {
						required: true,
						min: 6
					}
				},
				processLogin: false
			}
		},
		methods: {
			toggleNavbar () {
				document.body.classList.toggle('nav-open')
			},
			closeMenu () {
				document.body.classList.remove('nav-open')
				document.body.classList.remove('off-canvas-sidebar')
			},
			validate () {
				this.$validator.validateAll().then(isValid => {
					if (isValid) {
						this.login()
					}
				})
			},
			login () {
				var self = this
				swal({
					title: 'Вход',
					text: 'Авторизация пользователя в системе...',
					onOpen: () => {
						swal.showLoading()
					},
					showConfirmButton: false,
					allowOutsideClick: false,
					allowEscapeKey: false,
					allowEnterKey: false
				})
				self.processLogin = true
				this.$store.dispatch('userLogin', {
					account: this.model,
					callback: function () {
						self.$router.push({ path: '/' })
						swal.close()
					},
					successFalse: function (data) {
						var error = (data.message === 'Failed to fetch')
							? 'Невозможно сделать запрос, нет доступа к серверу или интернету' : data.message
						swal({
							title: 'Ошибка',
							text: error,
							type: 'error',
							showConfirmButton: true
						})
					}
				})
			}
		},
		beforeDestroy () {
			this.closeMenu()
		}
	}
</script>
<style>
</style>



